.go-to-cart {
  img { width: auto;}
  padding: 10px 15px;
  background: $color_green;
  border-radius: 8px;
  box-shadow: 0px 10px 16px rgba(0,0,0,0.16);
  position: absolute;
  top: 150px;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  h5 { padding: 0px 15px; font-weight: $font_weight_700;}
  .btn-1 {
    padding: 10px 25px;
    background: $color_white;
    color: $color_green;
    font-size: 12px;
    line-height: 15px;
    &:hover { background: $color_light_black; color: $color_white;}
  }
}
.artist-panel {
  padding: 10px 15px;
  background: $color_light_black;
  h5 {
    color: $color_white;
  }
  h6{ 
    a { 
      color: $color_white;
      i { padding-left: 5px;}
      &:hover { color: #dddddd !important;}
    }
  }
  .btn-outline {
    padding: 8px 15px;
    border-color: $color_grey;
    color: $color_white;
    &:hover {
      border-color: $color_white;
      background: $color_white;
      color: $color_light_black !important;
    }
  }
}
.artist-small-img {
  margin: 0px 10px;
  height: 50px;
  width: 50px;
  background-size: cover !important;
  background-position: center center !important;
  background-repeat: no-repeat !important;
}
.product-details-filter {
  .product-nm { 
    img { margin-left: 15px; width: auto; vertical-align: sub;}
    span { color: $color_red;}
    .breadcrumbs{
      padding-bottom: 8px;
      li{
        padding: 0 10px 0 15px;
        display: inline-block;
        position: relative;
        &::before { content: '>'; position: absolute; top: 0; left: 0;}
        &:first-child {
          padding-left: 0;
          &::before{ display: none;}
        }
      }
      li,a { 
        font-style: 12px;
        line-height: 16px;
        color: $color_light_black;
        font-weight: $font_weight_500;
      }
    }
  }
  .tshirt-sides-img {
    padding-top: 25px;
    .teesmall-img-blk {
      width: 20%; flex-basis: 20%; -webkit-lex-basis: 20%;
      img { height: 64px; width: 64px; object-fit: contain;}
      .btn-outline {
        margin-bottom: 15px;
        padding: 12px;
        border-color: #dadada;
        border-radius: 4px;
        &:hover, &:focus {
          background: transparent;
          border-color: $color_grey;
        }
      }
    }
    .teelarge-img-blk {
      padding: 25px;
      width: 80%; flex-basis: 80%; -webkit-lex-basis: 80%;
    }
  }
  .product-details-filter-types{
    .form-group { margin-bottom: 0;}
    margin-bottom: 35px;
    &:last-child { margin-bottom: 0;}
    .filter-title {
      width: 25%; flex-basis: 25%; -webkit-lex-basis: 25%;
      h5 { letter-spacing: 2px; text-transform: uppercase;}
    }
    .filter-selection {
      width: 75%; flex-basis: 75%; -webkit-lex-basis: 75%;
    }
    .role-select {
      label { 
        padding-left: 55px; font-size: 13px; text-transform: capitalize;
        img { margin-right: 15px;}
      }
    }
    .type-select {
      li {
        width: calc(100% / 2 - 16px);
        margin-bottom: 15px;
        min-width: 150px;
        input[type="radio"]{ 
          border-radius: 50px;
          &:checked { 
            &::before { top: 18px;}
          }
        }
        label {
          padding: 15px 12px 15px 45px;
          min-height: auto;
          img { width: 25px;}
          justify-content: flex-start; -webkit-justify-content: flex-start;
        }
        .tank-img { width: 20px;}
      }
    }
    .size-select{
      img { width: auto;}
      li {
        width: 40px;
        height: 40px;
        input[type="radio"]{ 
          border-radius: 50px;
          &:checked { 
            &::before { display: none;}
          }
        }
        label {
          font-size: 14px;
          padding: 10px 21px;
          min-height: 40px;
        }
      }
      .size-chart {
        margin-top: 10px;
        a { padding-left: 10px; font-weight: $font_weight_600;}
      }
    }
    .quantity-select {
      background: #f4f4f4;
      display: inline-block;
      border-radius: 8px;
      li { 
        display: inline-block; vertical-align: middle;
        span { padding: 6px 20px; font-size: 16px; line-height: 18px; font-weight: $font_weight_500;}
      }
      .btn {
        padding: 5px;
        height: 32px;
        width: 32px;
        background-color:  $color_white;
        color: $color_light_black;
        border-color: #DADADA;
        border-radius: 8px;
        font-size: 22px;
        line-height: 22px;
        &:hover {
          background-color: $color_blue;
          color: $color_white !important;
          border-color: $color_blue;
        }
      }
    }
    .product-price {
      li { 
        padding-right: 10px; display: inline-block; vertical-align: middle;
      }
      .old-price { display: none; color: #B9B9B9;}
    }
  }
  .add-cart-blk {
    .btn.add-wishlist {
      padding: 12px;
      background: #fff;
      height: 48px;
      width: 48px;
      min-width: auto;
      border-radius: 50px;
      border-color: #B9B9B9;
      img { 
          object-fit: none; object-position: 0 0px; width: 20px; height: 18px;
      }
      &:hover img {
          object-position: 0 -18px;
      }
    }
    .btn { 
      margin-right: 15px;
      min-width: 167px;
      &.btn-outline { 
        padding: 12px 17px;
        border-color: #B9B9B9;
        img { margin-right: 10px; width: 25px;}
        &:hover img {filter: brightness(100);}
      }
    }
  }
}
.product-desc {
  padding: 25px;
  background: #f4f4f4;
  h5 { padding-bottom: 15px; text-transform: uppercase; letter-spacing: 2px;}
  p { font-size: 14px;}
}
.reviews-list {
  img { width: 20px !important; vertical-align: sub;}
  h5 { padding-left: 15px;}
  .reviews-blk {
    margin-top: 15px;
    .review-user-img { 
      margin-right: 15px;
      width: 48px;
      height: 48px;
      border-radius: 50px;
      background-repeat: no-repeat !important;
      background-position: center center !important;
      background-size: cover !important;
    }
    .reviews-info {
      padding-bottom: 15px; 
      border-bottom: 1px solid #dadada;
      h5 { padding: 0 15px 0 0; font-weight: $font_weight_600;}
      p { margin: 10px 0 0; font-size: 14px; letter-spacing: 1px;}
    }
  }
  .btn-outline {
    margin-top: 35px;
    border-color: #B9B9B9;
  }
}
.product-details-reviews {
  padding: 50px 0 70px;
}
.products-from-artist{
  background: #f9f9f9;
}
/* Size Chart Modal */
.size-chart-modal {
  .sizechart-tab {
    text-align: center;
    .nav-tabs {
      margin-bottom: 25px;
      display: inline-flex;
      justify-content: center;
      -webkit-justify-content: center;
      border: 1px solid $color_blue;
      border-radius: 19px;
      .nav-link {
        margin-bottom: 0;
        padding: 5px 10px;
        border: none;
        border-radius: 19px;
        font-size: 14px;
        line-height: 22px;
        font-weight: $font_weight_700;
        color: $color_blue;
        width: 85px;
        transition-duration: .3s;
        cursor: pointer;
        &.active {
          background-color: $color_blue;
          color: $color_white !important;
        }
      }
    }
  }
  .chart-table {
    width: 100%;
    text-align: center;
    th,td { padding: 5px 10px;}
    th { 
      border-right: 1px solid #dadada;
      font-weight: $font_weight_600;
      &:last-child { border-right: none;}
    }
    td { 
      border-top: 1px solid #dadada; border-right: 1px solid #dadada; font-weight: $font_weight_500;
      &:last-child { border-right: none;}
    }
  }
}
/*Color Popup*/
.color-popup {
  button.btn { margin: 0px 25px 0 0 !important;}
  .product-colors-blk {
    cursor: pointer;
    position: relative;
    .more-color-popup {
      display: none;
      position: absolute;
      right: 35px;
      top: -40px;
      padding: 10px 0;
      min-width: 250px;
      margin-top: 15px;
      border: none;
      background-color: $color_white;
      box-shadow: 0px 6px 25px rgba(0,0,0,0.16);
      border-radius: 8px;
      text-align: center;
      z-index: 1040;
      .color-pallete button.btn {
        height: 34px;
        width: 34px;
        margin: 10px !important;
      }
      &::before {
        top: 25px;
        right: -20px;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-bottom-color: #fff;
        border-width: 10px;
        transform: rotate(90deg);
        -webkit-transform: rotate(90deg);
      }
    }
    &.color-popup-blk {
      .more-color-popup { display: block;}
    }
  }
}