.mycart-sec {
  padding: 35px 0 50px;
  h2 { padding-bottom: 35px;}
  .cart-list {
    margin-bottom: 18px;
    padding: 15px;
    border-radius: 10px;
    background: $color_white;
    box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
    .cart-product-img {
      margin-right: 25px;
      height: 90px;
      width: 90px;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      background-position: center center !important;
    }
    .cart-list-info {
      h6 a { padding-bottom: 20px; font-weight: $font_weight_600; color: $color_light_black;}
      .size, .color {
        margin-right: 50px;
        display: inline-block;
        span { font-weight: $font_weight_600; text-transform: capitalize;}
      }
      h5 { padding-right: 8px; text-transform: uppercase; letter-spacing: 2px;}
      .quantity-select {
        padding-top: 10px;
        li { 
          padding: 0px 5px; display: inline-block;
          span { padding: 8px; background: #f4f4f4; border-radius: 8px; font-weight: $font_weight_600;}
        }
        .btn {
          padding: 5px;
          height: 32px;
          width: 32px;
          background-color:  $color_white;
          color: $color_light_black;
          border-color: #DADADA;
          border-radius: 8px;
          font-size: 22px;
          line-height: 22px;
          &:hover {
            background-color: $color_blue;
            color: $color_white !important;
            border-color: $color_blue;
          }
        }
        .form-control{
          padding: 5px;
          max-width: 32px;
          max-height: 32px;
          border-radius: 8px !important;
          font-size: 16px;
          line-height: 19px;
          font-weight: $font_weight_600;
          &::-moz-placeholder{
            color: $color_light-black;
          }
          &::-webkit-input-placeholder{
            color: $color_light-black;
          }
          &::-o-placeholder{
            color: $color_light-black;
          }
          &:-ms-input-placeholder{
            color: $color_light-black;
          }
        }
      }
      .multiple-quantity-item {
          display: flex;
          align-items: center;
          &:not(:last-child){
            margin-bottom: 20px;
          }
          .quantity-select{
            padding-top: 0;
          }
          .size, .color{
            margin-right: 35px;
          }
      }
    }
    .cart-list-price {
      margin-left: 15px;
      text-align: right;
      .quantity-price { padding: 8px 0;}
      .btn {
        padding: 0 0 0 18px;
        background: transparent;
        border: none;
        img { 
            object-fit: none; object-position: 0 0px; width: 20px; height: 18px;
        }
        &:hover img {
            object-position: 0 -18px;
        }
        i { 
          padding-top: 3px;
          color: $color_light_black; font-size: 22px;
          &:hover { color: $color_red;}
        }
      }
    }
  }
  /***** Secure Note *****/
  .secure-note {
    padding: 13px 15px;
    border: 1px solid $color_blue;
    border-radius: 10px;
    img { margin-right: 15px; width: auto;}
    h5 { font-weight: $font_weight_600;}
  }
  /***** Card Css *****/
  .card { 
    margin: 0 0 18px 0;
    img { margin-right: 15px; width: auto;}
    border: none;
    border-radius: 10px;
    background: $color_white;
    box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
    .card-body {
      h6,h5 { font-weight: $font_weight_600;}
      a,p { font-size: 13px; font-weight: $font_weight_500;}
      a { 
        color: $color_red !important; text-transform: uppercase; font-weight: $font_weight_700;
        &:hover { text-decoration: underline;}
      }
      .checkbox-style {
        margin-left: 0;
        [type="checkbox"]:not(:checked) + label:before,
        [type="checkbox"]:checked + label:before {
          height: 20px; width: 20px;
        }
        [type="checkbox"]:not(:checked) + label:after,
        [type="checkbox"]:checked + label:after {
          top: 0; left: 3px;
        }
        label { padding-left: 28px !important; margin: 0; font-size: 16px; line-height: 24px; color: $color_light_black;}
        [type="checkbox"]:checked + label::before { background: $color_red;}
      }
      .card-text.apply-code {
        p,h5,h6 { padding-left: 28px;}
        h6 { padding-top: 15px; font-size: 12px; line-height: 16px; color: #b9b9b9; font-weight: $font_weight_500;}
      }
    }
    .btn-1 { width: 100%;}
    .form-group {
      position: relative;
      .form-control { 
        padding-right: 90px;
        &:focus { border-color: $color_red;}
      }
      .btn {
        padding: 11px 20px;
        border-radius: 24px;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
      }
    }
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    h2 { padding-bottom: 25px;}
  }
}
.btn-text {
  padding: 15px 20px;
  color: $color_red !important;
  font-weight: $font_weight_700;
}
/***** Promocode Window *****/
.promocode-window {
  margin: 0 0 0 auto;
  max-width: 498px;
  height: 100vh;
  right: -100%;
  transition: .4s ease-in-out !important;
  .modal-content { 
    height: 100%;
    border: none;
    border-radius: 0;
    padding: 35px 25px;
    background: $color_white;
    .modal-header { 
      padding: 0 0 20px 0; border: none; display: block;
      position: relative;
      .close{
        span {
          display: none;
        } 
        &::before { content: ''; background: url('../../images/close-ic2.svg') no-repeat; height: 20px; width: 20px; position: absolute; top: 10px; right: 0;}
      }
    }
    .modal-body {
      padding: 0;
      .form-group { 
        position: relative;
        margin: 20px 0;
        .btn { 
          padding: 11px 20px;
          border-radius: 24px;
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
        }
      }
      .card { 
        margin: 18px 0;
        img { margin-right: 15px; width: auto;}
        box-shadow: 2px 2px 20px rgba(0,0,0,0.04);
        border: none;
        h5 { padding: 5px 10px; background: #f4f4f4; font-weight: $font_weight_700 !important; display: inline-block; border-radius: 14px;}
        .small-font { margin-bottom: 0; padding-top: 10px; font-weight: $font_weight_300; color: $color_grey;}
        .btn-text { margin-left: 15px; padding: 0;}
      }
      .rcs-custom-scroll {
        .panel-content-custom {
          margin: 0;
          max-height: calc( 100vh - 180px);
          width: calc(100% - 15px);
        }
      }
    }
  }
}
.modal.show {
  .promocode-window { right: 0;}
}
/***** Price Summery *****/
.price-summery{
  li {
    padding: 5px 0 5px 0 !important;
    display: flex !important;
    display: -webkit-flex !important;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    font-weight: $font_weight_500;
    font-size: 14px;
    span {
      font-weight: $font_weight_600;
      letter-spacing: 1px;
    }
  }
  .total-pay {
    margin-top: 10px;
    padding: 15px 0 10px;
    border-top: 1px solid #DADADA;
  }
}