.login-right {
  h2 { text-align: left;}
  h6 { padding: 15px 0 25px; text-align: left;}
  .back-to-login { 
    margin-top: 15px;
    padding-top: 25px;
    border-top: 1px solid #dadada;
    text-align: center;
    a { 
        font-size: 12px; 
        line-height: 16px;
        color: $color_blue;
        text-transform: uppercase;
        font-weight: $font_weight_600;
        &:hover { text-decoration: underline;}
    }
  }
}

