.my-order-sec {
  h2 {
    font-size: 48px;
    line-height: 55px;
  }
  .order-tab {
    padding-bottom: 15px;
    border-bottom: 1px solid #b9b9b9;
    position: relative;
    .nav-tabs{
      border: none;
      position: absolute;
      right: 0;
      bottom: 0;
      .nav-item {
        .nav-link {
          padding: 5px 18px;
          border: none;
          color: $color_light_black;
          font-weight: $font_weight_700;
          min-width: 140px;
          text-align: center;
          cursor: pointer;
          &.active, &:hover {
            border: none;
            border-bottom: 2px solid $color_red;
            color: $color_red !important;
          }
        }
      }
    }
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    h2 { font-size: 40px; line-height: 48px;}
  }
}
/* Order Cards */
.card {
  margin: 18px 0;
  padding: 15px 15px 25px;
  img { width: auto;}
  h6 { font-weight: $font_weight_600;}
  li { 
    margin-bottom: 5px;
    padding-right: 30px;
    display: inline-block;
    font-size: 14px;
    line-height: 19px;
    color: $color_grey;
    font-weight: $font_weight_500;
    text-transform: capitalize;
    letter-spacing: 1px;
    span{
      font-weight: $font_weight_600;
      color: $color_light_black;
    }
  }
  /* Order Info */
  .order-info{
    padding-bottom: 15px;
    border-bottom: 1px solid #dadada;
    .btn-outline {
      margin-left: 15px;
      padding: 8px 25px;
      font-size: 12px;
      line-height: 19px;
      font-weight: $font_weight_600;
      flex: 0 1 175px;
    }
  }
  /* Order List */
  .orderd-product-list {
    padding: 15px;
    border-bottom: 1px solid #f4f4f4;
    .product-img {
      margin-right: 15px;
      height: 70px;
      width: 70px;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      background-position: center center !important;
    }
    h5 { font-weight: $font_weight_600;}
    li{
      padding-left: 50px;
      padding-right: 0;
      span { margin-top: 5px; display: block;}
    }
    a {
      padding-top: 15px;
      float: right;
      font-size: 10px;
      line-height: 15px;
      font-weight: $font_weight_500;
      color: $color_blue;
      &:hover { text-decoration: underline;}
    }
  }
  /* Pending Status */
  .pending-status {
    padding-top: 15px;
    h5 {
       font-weight: $font_weight_700; color: #FF9F49; text-transform: uppercase; letter-spacing: 1px;
       img { margin-right: 8px;}
      }
  }
  /* Total Tool Tip */
  .tooltip-box {
    position: relative;
    a { 
      padding-right: 10px; font-size: 20px; color: #b9b9b9;
    }
    .price-summery {
      display: none;
      padding: 15px;
      width: 250px;
      background: $color_white;
      box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
      border-radius: 10px;
      position: absolute;
      bottom: 35px;
      right: -10px;
      h4 { font-size: 12px;}
      li {
        padding: 5px 0;
        display: flex;
        display: -webkit-flex;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        font-size: 12px;
        color: $color_light_black;
      }
      .total-pay { 
        margin-top: 10px;
        padding: 10px 0 18px;
      }
      &::before { bottom: -18px; right: 18px; border: solid transparent; content: " "; height: 0; width: 0; position: absolute; pointer-events: none; border-top-color: $color_white; border-width: 10px;}
    }
    &:hover .price-summery { display: block;}
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    padding: 12px 15px;
    margin-top: 0;
    .price-summery {
      .total-pay {
        padding: 10px 0;
      }
    }
    .order-info {
      padding-bottom: 10px;
    }
  }
}
/* More Details Css*/
.more-details-sec {
  .pending-status {
    padding-top: 0px;
  }
}
.cancel-order{
  padding: 15px 0;
  background: $color_light_black;
  a{ color: $color_white !important;}
  h5{
    padding-left: 15px;
    color: #DADADA;
    span {font-weight: $font_weight_600; color: $color_white;}
  }
  .btn-outline {
    margin-left: 15px;
    padding: 10px 25px;
    border-color: $color_grey;
    color: $color_white;
  }
}
.more-details-card {
  .address-info {
    padding: 0;
    padding-top: 25px;
    h5 a { color: $color_light_black; font-weight: $font_weight_600;}
  }
  .total-info {
    display: flex; display: -webkit-flex; flex-direction: column; -webkit-flex-direction: column; align-items: flex-end; -webkit-align-items: flex-end;
    h5 { padding-bottom: 8px; color: $color_light_black; text-align: right;}
  }
  .btn-text { 
    float: right;
    padding: 20px 0 0;
    font-size: 14px;
    line-height: 19px;
    color: $color_blue !important;
  }
}
/* Rating Modal Css*/
.rating-modal {
  h6 {
    font-size: 16px;
    font-weight: $font_weight_700;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
}
.rating {
  margin-right: 15px;
  float:left;
}
.rating:not(:checked) > input {
  position:absolute;
  top:-9999px;
  clip:rect(0,0,0,0);
}
.rating:not(:checked) > label {
  float:right;
  width: 24px;
  padding: 0;
  margin: 0 12px 0 0;
  overflow:hidden;
  white-space:nowrap;
  cursor:pointer;
  font-size: 24px;
  line-height:28px;
  color:#dadada;
}
.rating:not(:checked) > label:before {
  content: '\f005 ';
  font-family: 'FontAwesome';
}
.rating > input:checked ~ label {
  color: #FF654F;
}
.rating:not(:checked) > label:hover {
  color: #FF654F;
}
.rating > label:active {
  position:relative;
  top:2px;
  left:2px;
}
/* Return Order */
input[type="file"] { display: none;}  
.add-img {
  margin: 10px 0 25px;
  .box {
    margin-right: 15px;
    border-radius: 12px;
    position: relative;
    label { 
      margin: 0;
      .btn.delete-ic { 
        padding: 0; position: absolute; top: -8px; right: -8px; background: transparent; border: none;
        &::before { content: "\f00d"; background: $color_red; color: #fff; height: 20px; width: 20px; text-align: center; font-size: 13px; display: block; font-family: 'FontAwesome'; border-radius: 50%;}
      }
    }
    .add-img-blk {
      padding: 20px 12px;
      border: 2px dashed #dadada;
      text-align: center;
      border-radius: 12px;
      cursor: pointer;
      img { width: auto;}
      p { margin: 0; padding-top: 15px; color: #b9b9b9; font-weight: $font_weight_500; text-transform: capitalize;}
    }
    .return-img {
      height: 99px;
      width: 99px;
      background-size: cover !important;
      background-repeat: no-repeat !important;
      background-position: center center !important;
      border-radius: 12px;
    }
  }
} 