h1,h2,h3,h4,h5,h6           { margin: 0;}
h1                          { font-size: 60px; line-height: 73px; font-weight: $font_weight_700;}
h2							{ font-size: 34px; line-height: 42px; font-weight: $font_weight_600;}
h3							{ font-size: 24px; line-height: 34px; font-weight: $font_weight_600;}
h4							{ font-size: 20px; line-height: 24px; font-weight: $font_weight_600;}
h5							{ font-size: 14px; line-height: 19px; font-weight: $font_weight_500;}
h6                          { font-size: 16px; line-height: 24px; font-weight: $font_weight_500;}
p							{ font-size: 16px; line-height: 22px; font-weight: $font_weight_500;}
p.large-font                { font-size: 16px; line-height: 27px;}
p.small-font                { font-size: 12px; line-height: 16px; font-weight: $font_weight_500;}


/** Responsive **/
@media (min-width: 1200px) and (max-width: 1499px){
    h3 { font-size: 22px; line-height: 30px;}
    h4 { font-size: 16px; line-height: 22px;}
    h6 { font-size: 14px; line-height: 22px;}
}

