.artist-details {
  border-bottom: 1px solid #dadada;
  .artist-info {
    padding-bottom: 35px;
    .artist-img {
      margin-right: 25px;
      background-size: cover !important; 
      background-position: center center !important;
      background-repeat: no-repeat !important;
      width: 120px; 
      height: 120px;
      border-radius: 50%;
    }
    .artist-social {
      padding-top: 10px;
      li {
        padding-right: 15px;
        display: inline-block;
        a {
          font-size: 24px;
          line-height: 28px;
          color: #DADADA;
        }
      }
    }
    .btn-outline {
      padding: 8px 20px;
      border-color: $color_blue;
      color: $color_blue;
    }
  }
  .artist-brief-info {
    padding-bottom: 35px;
    h6 { color: #dadada;}
    p { margin: 10px 0; line-height: 25px; font-weight: $font_weight_500;}
    a { color: $color_blue;}
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    .artist-brief-info {
      padding-bottom: 30px;
    }
  }
}
.my-design-sec {
  padding-bottom: 70px;
  .products-blk {
    .artist-nm { display: none;}
  }
}