.my-account-blks {
  h2 { margin-bottom: 35px;}
  .card { margin: 0;}
  /* Account Links Css */
  .account-links-blk {
    .account-item-list {
      .card{
        padding: 0;
        .card-body {
          li {
            padding: 12px 0;
            display: block;
            a {
              padding-left: 25px; 
              font-size: 16px;
              line-height: 22px;
              font-weight: $font_weight_600;
              color: $color_light_black;
              position: relative;
              &:hover, &.active{ 
                  color: $color_blue;
                  &::before{ content: ''; background: $color_blue; height: 16px; width: 8px; position: absolute; top: 50%; left: 0; transform: translateY(-50%); -webkit-transform: translateY(-50%);}
              }
            }
          }
        }
      }
    }
  }
  .my-account-content h3 { padding-bottom: 25px;}

  /* Profile Information Css */
  .user-profile-details {
    margin-bottom: 25px;
    .user-img {
      margin-right: 25px;
      height: 149px;
      width: 149px;
      border-radius: 50%;
      background-size: cover !important;
      background-repeat: no-repeat !important;
      background-position: center center !important;
      position: relative;
      .box label {
        position: absolute;
        bottom: 0;
        right: 0;
        cursor: pointer;
      }
    }
    h3 { padding: 0; font-weight: $font_weight_700;}
    h6 { padding: 10px 0; color: #B9B9B9; font-weight: $font_weight_500;}
    a { 
      color: $color_blue; font-weight: $font_weight_700; text-transform: uppercase;
      &:hover { text-decoration: underline;}
    }
  }
  .form-field {
    .btn {
      margin: 15px;
      min-width: 217px;
      &.btn-outline {
        border-color: $color_blue;
        color: $color_blue;
      }
    }
  }
  .unfollowing-artist {
    border-top: 1px solid #b9b9b9;
    h3 { padding: 25px 0;}
    .artist-unfollow-list {
      margin: 15px 0;
      padding-bottom: 15px;
      border-bottom: 1px solid #dadada;
      .artist-img {
        margin-right: 15px;
        height: 72px;
        width: 72px;
        border-radius: 50%;
        background-size: cover !important;
        background-repeat: no-repeat !important;
        background-position: center center !important;
      }
      h6 { padding-top: 5px; color: #b9b9b9; font-weight: $font_weight_500;}
      .btn-outline {
        padding: 10px 25px 8px;
        border-color: #dadada;
        color: $color_light_black;
        font-size: 12px;
        line-height: 16px;
        font-weight: $font_weight_600;
      }
    }
  }

  /* Address List Css */
  .address-blk-list {
    margin-bottom: 15px;
    padding: 15px;
    border: 1px solid #DADADA;
    border-radius: 10px;
    h6 { font-weight: $font_weight_600;}
    .address-tag {
      margin: 0px 15px;
    }
    .address-info {
      padding: 15px 0 0;
    }
    &.btn-outline { 
      width: 100%; 
      color: $color_blue;
      img {
          object-fit: none; object-position: 0 0px; width: 20px; height: 20px;
        }
      &:hover img {
        object-position: 0 -20px;
      } 
    }
  }
  .edit-dlt-ic {
    padding: 10px 0;
    font-size: 14px;
    font-weight: $font_weight_700;
    color: $color_blue !important;
    &:hover { text-decoration: underline;}
    &:last-child { color: #FF0000 !important;}
    img {
      margin-right: 10px;
    }
  }
  .add-address-sec {
    .add-new-btn, .card {
      border: 1px solid #dadada;
      box-shadow: none;
    }
    .add-new-btn:hover { border-color: $color_blue;}
  }

  /* Reviews Css */
  .reviews-blk-list {
    .prod-img {
      margin-right: 15px;
      height: 100px;
      width: 100px;
      min-width: 100px;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      background-position: center center !important;
    }
    .review-text {
      h5,p {
        padding-top: 10px; 
        font-size: 12px;
        line-height: 19px;
        font-weight: $font_weight_500;
      }
      h5 { color: $color_grey;}
    }
    .total-review-digit { 
      img { margin-right: 5px;}
      span { vertical-align: middle;}
    }
    .edit-dlt-ic { padding: 0 35px 0 0;}
  }

  /* Notification Css */
  .notification-blk-list {
    p { margin-bottom: 0;}
    .notification-icons {
      margin-right: 15px;
      img { height: 80px; width: 80px; object-fit: cover;}
    }
  }

  /* Tokens Css */
  .token-blk {
    .token-content {
      padding-bottom: 60px;
      background: url('../../../images/token-bg.svg') no-repeat right bottom / contain;
      h3 { padding: 0;}
      .card-text {
        width: 417px;
      }
      .token-balance {
        position: relative;
        h6 { font-weight: $font_weight_500;}
        h2 { margin: 15px 0 0; color: $color_blue;}
        margin: 20px 0;
        padding: 15px;
        border: 2px dashed #dadada;
        border-radius: 12px;
        img { position: absolute; right: 0; bottom: 0;}
      }
    }
    ul {
      li { 
        padding: 3px 0 3px 12px; 
        font-size: 10px; 
        line-height: 19px;
        text-transform: none;
        color: $color_light_black;
        position: relative;
        &::before { content: ''; background: #000000; height: 5px; width: 5px; top: 10px; left: 0; position: absolute; display: block; border-radius: 50%;}
      }
    }
    .token-table {
      padding: 0;
      margin-top: 25px;
      h3 { padding: 15px;}
      table {
        margin: 15px 0;
        width: 100%;
        th,td { padding: 12px 15px; border: 1px solid #f4f4f4; color: $color_light_black;}
        th,td { 
          &:last-child { text-align: center;}
        }
        th    { font-size: 14px; line-height: 19px; text-transform: uppercase; letter-spacing: 1px;}
        td    { text-transform: capitalize;}
        .dabit-token { color: $color_red; font-weight: $font_weight_600;}
        .credit-token { color: $color_green; font-weight: $font_weight_600;}
      }
    }
  }
}